export default {
  name: "curriculum",
  data() {
    return {
      option: {
        firstList: [
          { name: "全部课程", class: "", type: true },
          { name: "报名中", class: "", type: false },
          { name: "直播中", class: "", type: false },
          { name: "已结束", class: "", type: false }
        ],
        lastList: [
          { name: "最新推荐", class: "", type: true },
          { name: "最热推荐", class: "", type: false }
        ]
      }
    };
  },
  //   直接执行
  mounted() {},
  //   方法
  methods: {
    // 公用的选择方法
    selectRankingAll: function(item, index, ListName) {
      const _this = this;
      _this.option[ListName].map(function(event, idx) {
        if (event.name == item.name) {
          event.type = true;
          _this.$set(_this.option[ListName], idx, event);
        } else {
          event.type = false;
          _this.$set(_this.option[ListName], idx, event);
        }
      });
    },
    goOfficialCourses: function(name) {
      if (name) {
        if (name == "官方") {
          this.$router.push({ path: "/officialCourses" });
        } else if ("非官方") {
          this.$router.push({ path: "/unofficialCourses" });
        }
      }
    },
    demo:function(){
         this.$router.push({ path: "/curriculum/task" });
    }
  }
};



